    style="position: fixed; width: 100%; z-index: 3; min-height: 80px; top: 0;padding-left:0;"
  >
    <div class="" style="width: 100%; display: flex">
      <div class="col-9">
        <a class="navbar-brand" href="/"> 

        <img src="@/assets/logo.png" :width="120" /></a>
      </div>
      <div class="col-1 center">
        <a v-if="!isEnglish" 
        data-toggle="collapse"
          data-target="#navbarNavAltMarkup"
