          >AZ</a
        >
      </div>
      <div class="col-2 center">
        <button
          class="navbar-toggler"
          type="button"
          data-toggle="collapse"
          data-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        >
          <span class="navbar-toggler-icon"></span>
        </button>
      </div>
    </div>

    <div class="collapse navbar-collapse" id="navbarNavAltMarkup" style="padding-top: 15px;">
      <div class="navbar-nav text-center">
        <a class="nav-item nav-link active" data-toggle="collapse"
          data-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        v-if="isEnglish" @click="changeHomeToContact" to="/"
          >Home <span class="sr-only">(current)</span></a
        >
        <a class="nav-item nav-link active"
        data-toggle="collapse"
          data-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation" v-if="!isEnglish"  @click="changeHomeToContact" to="/"
          >Ana Səhifə <span class="sr-only">(current)</span></a
        >
        <a class="nav-item nav-link active" 
        data-toggle="collapse"
          data-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation" v-if="isEnglish"  @click="changeContactToHome" to="/contact"
          >Contact</a
        >
        <a class="nav-item nav-link active" data-toggle="collapse"
          data-target="#navbarNavAltMarkup"
          aria-controls="navbarNavAltMarkup"
          aria-expanded="false"
          aria-label="Toggle navigation"
        v-if="!isEnglish"  @click="changeContactToHome" to="/contact"
          >Əlaqə</a
        >
      </div>
    </div>
  </nav>
  
  <HelloWorld v-show="isHome"  :categories="categories" :isEnglish="isEnglish"/>
  <div v-if="isEnglish">
    <Contact v-if="!isHome" :isEnglish="true"/>
  </div>
  <div v-if="!isEnglish">
    <Contact v-if="!isHome" :isEnglish="false"/>
  </div>
  <footer class="page-footer font-small special-color-dark pt-4" style="margin-top: 50px;">

    <!-- Footer Elements -->
