      <!-- Social buttons -->
      <ul class="list-unstyled list-inline text-center">
        <li class="list-inline-item">
          <a class="btn-floating btn-fb mx-1" href="https://www.facebook.com/Resanthotels/" target="_blank">
            <i class="fab fa-facebook-f"> </i>
          </a>
        </li>
        <li class="list-inline-item">
          <a class="btn-floating btn-tw mx-1" href="https://az.linkedin.com/company/resant-hotels-resorts" target="_blank">
            <i class="fab fa-linkedin"> </i>
          </a>
        </li>
        <li class="list-inline-item">
          <a class="btn-floating btn-gplus mx-1" href="https://www.instagram.com/resantgrouphotels" target="_blank">
            <i class="fab fa-instagram"> </i>
          </a>
        </li>
        <li class="list-inline-item">
          <a class="btn-floating btn-li mx-1" href="https://www.youtube.com/channel/UCf3T1d_G3h3B1SKj5QSKw-A/featured" target="_blank">
            <i class="fab fa-youtube"> </i>
          </a>
        </li>
       
      </ul>
      <!-- Social buttons -->
  
    </div>
    <!-- Footer Elements -->
  
    <!-- Copyright -->
    <div class="footer-copyright text-center py-3">© 2020 Copyright:
      <a href="https://resanthotels.az/" target="_blank"> www.resanthotels.az</a>
    </div>
    <!-- Copyright -->
  
  </footer>
  <button  v-show="fab" @click="toTop" class="go-back">
   <i class="fas fa-level-up-alt"></i>
</button>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";
import Contact from "./components/Contact.vue"
import $ from "jquery";
import "./assets/style.css";
import "./assets/contact-style.css";
import "./assets/eat.png";
import "./assets/scripts.js";
import json from "./components/data.json";

export default {
  created () {
    $('[data-toggle="collapse"]').on('click',function(e){
    if ( $(this).parents('.accordion').find('.collapse.show') ){
        var idx = $(this).index('[data-toggle="collapse"]');
        if (idx == $('.collapse.show').index('.collapse')) {
            // prevent collapse
            e.stopPropagation();
        }
    }
});
    $(window).scroll(function(e){
       if (typeof window === 'undefined') return
      const top = window.pageYOffset ||   e.target.scrollTop || 0
      this.fab = top > 20
      if(this.fab){
        $(".go-back").show(500)
      }else{
        $(".go-back").hide(500)
      }
    })
  },
  data () {
    return {
      categories:json,
      isEnglish:false,
      isHome:true
    }
  },
  methods: {
    changeContactToHome(){
      console.log("Yeah is Home false");
      this.isHome=false;
    },
    changeHomeToContact(){
      console.log("Yeah is Home true");

      this.isHome=true;
    },
    showPage() {
      document.getElementById("loader").style.display = "none";
      document.getElementById("app").style.display = "block";
    },
    changeLanguage() {
      this.isEnglish = !this.isEnglish;
      document.getElementById("app").style.display = "none";
      document.getElementById("loader").style.display = "block";
      var myVar;
      myVar = setTimeout(function () {
        console.log(
          document
            .getElementById("body")
            .getElementsByClassName("nav-link")[0]
            .setAttribute("aria-expanded", "true")
        );
        document
          .getElementById("body")
          .getElementsByClassName("collapse")[0]
          .classList.toggle("show");
        document.getElementById("app").style.display = "block";
        document.getElementById("loader").style.display = "none";
        myVar = !myVar;
      }, 3000);
    },
    toTop () {
       $('html, body').animate({ scrollTop: 0 }, 'slow', function () {
    })}
  },
  mounted() {
    window.addEventListener("load", () => {
      var elements = document
        .getElementById("body")
        .getElementsByClassName("collapse");
      elements[0].classList.toggle("show");
    });
     document
          .getElementById("body")
          .getElementsByClassName("nav-link")[0]
          .setAttribute("aria-expanded", "true")
    this.showPage();
  },

  name: "App",
  components: {
    HelloWorld,
    Contact
  },
};
</script>

